  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}

/*
    My Styles
*/
body
{
    background-color: #000000;
}

header
{
    background-color: #1A0033;
    text-align: center;
}
.logo
{
    font-weight: 600;
    color: #00FF87;
    font-size: x-large;
}
.header
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    height: 100px;
    align-items: center; 
}
.buttons
{
    display: flex;
    gap: 20px;
}
.buttons button
{
    color: #F5F5F5;
    background-color: #1A0033;
    border: 2px solid brown;
    border-radius: 10px;
}
button:hover
{
    transform: scale(1.05);
    background-color: #FF4B6E;
}

#content
{
    color: #FF4B6E;
    padding: 30px;
    display: grid;
    grid-template-rows: repeat(5,min-content);
    gap: 30px;
    width: 65ch;
    justify-self: center;
    font-size: large;
    
}
.dish,.desc,.more_info,.contact ,.employee
    {
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
        border: 2px solid #FF4B6E;
        border-radius: 12px;
        padding: 20px;
    }
.para_heading
{
    font-size: x-large;
    margin-bottom: 20px;
}
li
{
    list-style-type: none;
}
li::before
{
    content: "- ";
}
.contact>ul>li::before
{
    content: "";
}

.dish,.employee
{
    display: grid;
    grid-template-columns: 120px 40ch;
    grid-template-rows: min-content;
    img {
        width: 100%;
        height: 100%;
        border-radius: 49%;
        object-fit: cover;
    }
    padding: 12px;
    
}
.dish .para_heading,.employee .para_heading
{
    grid-column: 1/-1;
    text-align: center;
}
.img_div 
{
    width: 100%;
    height: 130px;
    grid-row: 2/-1;
    align-self: center;
    
}
.employee .img_div
{
  img 
  {
    width: 100px;
    height: 100px;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;CAGC;;AAED;EACE,sBAAsB;AACxB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE;IACE,gCAAgC;EAClC;AACF;;AAEA;EACE,gBAAgB;EAChB,mCAAmC;AACrC;;AAEA;EACE,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,iBAAiB;AACnB;AACA;EACE,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;;CAEC;AACD;;IAEI,yBAAyB;AAC7B;;AAEA;;IAEI,yBAAyB;IACzB,kBAAkB;AACtB;AACA;;IAEI,gBAAgB;IAChB,cAAc;IACd,kBAAkB;AACtB;AACA;;IAEI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;IACT,aAAa;IACb,mBAAmB;AACvB;AACA;;IAEI,aAAa;IACb,SAAS;AACb;AACA;;IAEI,cAAc;IACd,yBAAyB;IACzB,uBAAuB;IACvB,mBAAmB;AACvB;AACA;;IAEI,sBAAsB;IACtB,yBAAyB;AAC7B;;AAEA;;IAEI,cAAc;IACd,aAAa;IACb,aAAa;IACb,yCAAyC;IACzC,SAAS;IACT,WAAW;IACX,oBAAoB;IACpB,gBAAgB;;AAEpB;AACA;;QAEQ,wCAAwC;QACxC,yBAAyB;QACzB,mBAAmB;QACnB,aAAa;IACjB;AACJ;;IAEI,kBAAkB;IAClB,mBAAmB;AACvB;AACA;;IAEI,qBAAqB;AACzB;AACA;;IAEI,aAAa;AACjB;AACA;;IAEI,WAAW;AACf;;AAEA;;IAEI,aAAa;IACb,iCAAiC;IACjC,+BAA+B;IAC/B;QACI,WAAW;QACX,YAAY;QACZ,kBAAkB;QAClB,iBAAiB;IACrB;IACA,aAAa;;AAEjB;AACA;;IAEI,iBAAiB;IACjB,kBAAkB;AACtB;AACA;;IAEI,WAAW;IACX,aAAa;IACb,cAAc;IACd,kBAAkB;;AAEtB;AACA;;EAEE;;IAEE,YAAY;IACZ,aAAa;EACf;AACF","sourcesContent":["/*\n  Josh's Custom CSS Reset\n  https://www.joshwcomeau.com/css/custom-css-reset/\n*/\n\n*, *::before, *::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n}\n\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\n\nimg, picture, video, canvas, svg {\n  display: block;\n  max-width: 100%;\n}\n\ninput, button, textarea, select {\n  font: inherit;\n}\n\np, h1, h2, h3, h4, h5, h6 {\n  overflow-wrap: break-word;\n}\n\np {\n  text-wrap: pretty;\n}\nh1, h2, h3, h4, h5, h6 {\n  text-wrap: balance;\n}\n\n#root, #__next {\n  isolation: isolate;\n}\n\n/*\n    My Styles\n*/\nbody\n{\n    background-color: #000000;\n}\n\nheader\n{\n    bac